
import { defineComponent } from "vue";
import { ActiveLoader } from "vue-loading-overlay";

import moment from "moment";

import { getNextFibonacci } from "@/common";
import { getReview, saveReview } from "@/firebase";

import Document from "@/components/Document.vue";

var loader: ActiveLoader;

export default defineComponent({
    components: {
        Document,
    },
    methods: {
        loadReview(loading: boolean) {
            if (loading) this.showLoading();

            getReview()
                .then((querySnapshot) => {
                    this.total = querySnapshot.docs.length;

                    if (this.total) {
                        this.data = querySnapshot.docs[0].data();

                        this.nextFibonacci = getNextFibonacci(
                            this.data.last_fibonacci
                        );

                        this.next = moment()
                            .add(this.nextFibonacci, "days")
                            .format("YYYY-MM-DD");
                        this.keep = moment()
                            .add(this.data.last_fibonacci, "days")
                            .format("YYYY-MM-DD");
                        this.tmr = moment().add(1, "days").format("YYYY-MM-DD");
                    }
                })
                .catch((err) => {
                    console.log(err);

                    this.errorToast("Error while loading review.");
                })
                .finally(() => {
                    if (loading) this.hideLoading();
                });
        },
        save(mode: string) {
            this.showLoading();

            let update: any = new Object();
            update.last_review = moment().format("YYYY-MM-DD");

            if (mode == "next") {
                update.last_fibonacci = this.nextFibonacci;
                update.next_review = this.next;
            } else if (mode == "keep") {
                update.next_review = this.keep;
            } else {
                // tmr
                update.next_review = this.tmr;
            }

            saveReview(this.data.word, update)
                .then(() => {
                    this.loadReview(false);
                })
                .catch((err) => {
                    console.log(err);

                    this.errorToast("Error while updating word.");
                })
                .finally(() => {
                    this.hideLoading();
                });
        },
        showLoading() {
            loader = this.$loading.show({ color: "#198964" });
        },
        hideLoading() {
            loader.hide();
        },
        errorToast(detail: string) {
            this.$toast.add({
                severity: "error",
                summary: "Error",
                detail: detail,
                life: 3000,
            });
        },
    },
    data() {
        return {
            total: 0,
            data: {
                word: "",
                last_fibonacci: 0,
            },
            nextFibonacci: 0,
            next: "",
            keep: "",
            tmr: "",
        };
    },
    mounted() {
        this.loadReview(true);
    },
});
