<template>
    <div class="row">
        <div class="col">
            <router-link :to="'/new/' + data.word">
                {{ data.word }}
            </router-link>
            <span class="btn-toggle" @click="toggle"
                >[show {{ showSummary ? "detail" : "summary" }}]
            </span>
        </div>
    </div>

    <template v-if="showSummary">
        <template v-for="(meaning, index) in data.meanings" :key="index">
            <div v-if="meaning.part_of_speech" class="row">
                <div class="col">
                    [{{ meaning.part_of_speech }}]
                    <template
                        v-for="(definition, index) in meaning.definitions"
                        :key="index"
                    >
                        [{{ definition.definition }}]
                    </template>
                </div>
            </div>
        </template>
    </template>

    <template v-else>
        <template v-for="(meaning, index) in data.meanings" :key="index">
            <SingleLine prefix="[" suffix="]" :data="meaning.part_of_speech" />

            <template
                v-for="(definition, index) in meaning.definitions"
                :key="index"
            >
                <SingleLine
                    prefix="Definition: "
                    extra="col-ps-1"
                    :data="definition.definition"
                />

                <template
                    v-for="(example, index) in definition.examples"
                    :key="index"
                >
                    <SingleLine
                        prefix='"'
                        suffix='"'
                        extra="col-ps-2 example"
                        :data="example.example"
                    />
                </template>
            </template>

            <LinkList
                title="Synonym"
                extra="col-ps-1"
                :data="meaning.synonyms"
            />

            <LinkList
                title="Antonym"
                extra="col-ps-1"
                :data="meaning.antonyms"
            />
        </template>

        <LinkList title="Family" :data="data.families" />
        <SingleLine prefix="Remark: " :data="data.remark" />
    </template>
</template>

<script lang="ts">
import { defineComponent } from "vue";

import SingleLine from "@/components/SingleLine.vue";
import LinkList from "@/components/LinkList.vue";

export default defineComponent({
    components: {
        SingleLine,
        LinkList,
    },
    props: {
        data: Object,
    },
    methods: {
        toggle() {
            this.showSummary = !this.showSummary;
        },
    },
    data() {
        return {
            showSummary: true,
        };
    },
});
</script>

<style scoped lang="less">
.btn-toggle {
    color: #0d6efd;
    text-decoration: underline;
    margin-left: 0.5rem;
    cursor: pointer;
}
</style>
