<template>
    <div id="review">
        <div class="row">
            <div class="col">
                <span class="review-ttl">{{ total }}</span>
                <span class="review-word">word(s) to be reviewed</span>
            </div>
        </div>

        <template v-if="total">
            <hr />

            <Document :data="data" />

            <hr />

            <div class="row">
                <div class="col">
                    <Button label="Next" @click="save('next')" />
                    <Button
                        label="Keep"
                        class="p-button-secondary"
                        @click="save('again')"
                    />
                    <Button
                        label="Tomorrow"
                        class="p-button-secondary"
                        @click="save('tmr')"
                    />
                </div>
            </div>

            <div class="row">
                <div class="col col-sm-i">
                    Next: {{ next }} | Keep: {{ keep }} | Tomorrow: {{ tmr }}
                </div>
            </div>
        </template>
    </div>
    <Toast />
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { ActiveLoader } from "vue-loading-overlay";

import moment from "moment";

import { getNextFibonacci } from "@/common";
import { getReview, saveReview } from "@/firebase";

import Document from "@/components/Document.vue";

var loader: ActiveLoader;

export default defineComponent({
    components: {
        Document,
    },
    methods: {
        loadReview(loading: boolean) {
            if (loading) this.showLoading();

            getReview()
                .then((querySnapshot) => {
                    this.total = querySnapshot.docs.length;

                    if (this.total) {
                        this.data = querySnapshot.docs[0].data();

                        this.nextFibonacci = getNextFibonacci(
                            this.data.last_fibonacci
                        );

                        this.next = moment()
                            .add(this.nextFibonacci, "days")
                            .format("YYYY-MM-DD");
                        this.keep = moment()
                            .add(this.data.last_fibonacci, "days")
                            .format("YYYY-MM-DD");
                        this.tmr = moment().add(1, "days").format("YYYY-MM-DD");
                    }
                })
                .catch((err) => {
                    console.log(err);

                    this.errorToast("Error while loading review.");
                })
                .finally(() => {
                    if (loading) this.hideLoading();
                });
        },
        save(mode: string) {
            this.showLoading();

            let update: any = new Object();
            update.last_review = moment().format("YYYY-MM-DD");

            if (mode == "next") {
                update.last_fibonacci = this.nextFibonacci;
                update.next_review = this.next;
            } else if (mode == "keep") {
                update.next_review = this.keep;
            } else {
                // tmr
                update.next_review = this.tmr;
            }

            saveReview(this.data.word, update)
                .then(() => {
                    this.loadReview(false);
                })
                .catch((err) => {
                    console.log(err);

                    this.errorToast("Error while updating word.");
                })
                .finally(() => {
                    this.hideLoading();
                });
        },
        showLoading() {
            loader = this.$loading.show({ color: "#198964" });
        },
        hideLoading() {
            loader.hide();
        },
        errorToast(detail: string) {
            this.$toast.add({
                severity: "error",
                summary: "Error",
                detail: detail,
                life: 3000,
            });
        },
    },
    data() {
        return {
            total: 0,
            data: {
                word: "",
                last_fibonacci: 0,
            },
            nextFibonacci: 0,
            next: "",
            keep: "",
            tmr: "",
        };
    },
    mounted() {
        this.loadReview(true);
    },
});
</script>

<style scoped lang="less">
#review {
    .review-ttl {
        font-size: 3rem;
        font-weight: bold;
        color: #198964;
        line-height: 2rem;
    }

    .review-word {
        font-size: 1.2rem;
        margin-left: 0.5rem;
    }
}
</style>
