<template>
    <div v-if="data && data.length" class="row">
        <div class="col" :class="extra">
            {{ title }}:
            <template v-for="(item, index) in data" :key="index">
                <router-link :to="'/new/' + item">
                    {{ item }}
                </router-link>
                <template v-if="index != data.length - 1"> | </template>
            </template>
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
    props: {
        data: Object,
        title: String,
        extra: String,
    },
});
</script>
