
import { defineComponent } from "vue";

import SingleLine from "@/components/SingleLine.vue";
import LinkList from "@/components/LinkList.vue";

export default defineComponent({
    components: {
        SingleLine,
        LinkList,
    },
    props: {
        data: Object,
    },
    methods: {
        toggle() {
            this.showSummary = !this.showSummary;
        },
    },
    data() {
        return {
            showSummary: true,
        };
    },
});
