<template>
    <div v-if="data" class="row">
        <div class="col" :class="extra">
            {{ prefix + data + suffix }}
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
    props: {
        data: String,
        prefix: {
            type: String,
            default: "",
        },
        suffix: {
            type: String,
            default: "",
        },
        extra: String,
    },
});
</script>
